@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,500,700&display=swap&subset=korean);
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(/static/media/MaterialIcons-Regular.e79bfd88.eot); /* For IE6-8 */
  src: local('Material Icons'),
       local('MaterialIcons-Regular'),
       url(/static/media/MaterialIcons-Regular.570eb838.woff2) format('woff2'),
       url(/static/media/MaterialIcons-Regular.012cf6a1.woff) format('woff'),
       url(/static/media/MaterialIcons-Regular.a37b0c01.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

*{margin:0;padding:0;box-sizing:border-box}html,body{font-size:16px;font-family:'Noto Sans KR', sans-serif}body{width:1920px}body,html,input,textarea,select,button{font-family:'Noto Sans KR', sans-serif}.height-100{height:100%}.systemAccountModify .MuiFormControl-root,.orgAccountModify .MuiFormControl-root,.memberAccountView .MuiFormControl-root,.orgAccountDialog .MuiFormControl-root,.systemAccountDialog .MuiFormControl-root{width:100%}.c-table{width:100%;border-spacing:0}.c-table thead th{padding:1em 0;border-bottom:1px solid #ccc}.c-table tbody tr td{text-align:center}.dt_input{width:100%}

body{margin:0;padding:0}.loading{width:100vw;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.loading .MuiCircularProgress-root{margin-bottom:24px;color:#3880ff}

.page-error{width:100vw;height:100vh;display:flex;align-items:center;justify-content:center;flex-direction:column}.page-error .buttons{margin-top:1rem}.page-error .buttons .MuiButton-root+.MuiButton-root{margin-left:1rem}

.nav{position:fixed;z-index:100;left:0;top:0;width:240px;height:100vh;background:#fff;overflow:auto;border-right:1px solid rgba(0,0,0,0.12)}.nav a{color:inherit;text-decoration:none}.nav .logo{border-bottom:1px solid rgba(0,0,0,0.12);text-align:center}.nav .logo img{width:80%}.nav .MuiListItem-root{border-radius:4px}.nav .active{color:#ff554a}.nav .parent .MuiListItem-root{padding-left:24px}.nav .parent .MuiListItem-root span{font-weight:600}.nav .children .MuiListItem-root{padding-left:40px}.nav .MuiDivider-root{margin:8px 0}.nav .group{position:relative}.nav .group .expandIcon{position:absolute;right:16px;top:8px;z-index:-1}

.MuiAppBar-root{width:calc(100% - 240px) !important}.header__logo{height:48px}

.template-dashboard{display:flex}.template-dashboard main{width:calc(100% - 240px);min-height:100vh;margin-left:240px;padding:94px 48px 24px 48px;background:#fafafa}.template-dashboard .breadcrumbs{font-size:1.2rem}.template-dashboard .breadcrumbs p{font-weight:bold;color:#000}.template-dashboard .breadcrumbs-box{border-radius:5px}

.start_end_date_picker{display:flex}.start_end_date_picker>span{font-size:2rem;margin:0 1rem;line-height:3.5rem}.start_end_date_picker .MuiInputBase-input{width:200px}

.custom_table.scrollable .MuiTable-root:first-child{width:2400px}.custom_table .toolbar{display:flex;align-items:center;justify-content:space-between}.custom_table .toolbar .MuiToolbar-root .MuiTypography-root{margin-right:24px}.custom_table .toolbar .MuiToolbar-root>div:last-child>div>div{display:flex}.custom_table .toolbar .start_end_date_picker{padding:24px 24px}.custom_table .customPageSizeInput{margin-left:16px}.custom_table .customPageSizeInput .MuiInput-underline:before,.custom_table .customPageSizeInput .MuiInput-underline:after{display:none}.custom_table .MuiTableRow-root:empty{display:none}.custom_table .MuiTableRow-root[style*="height: 980px;"],.custom_table .MuiTableRow-root[style*="height: 2450px;"],.custom_table .MuiTableRow-root[style*="height: 4900px;"]{height:200px !important}.custom_table .MuiTableSortLabel-root{white-space:nowrap}


.queryTraceDialog .empty{display:flex;align-items:center;justify-content:center;flex-direction:column;min-height:200px}.paging{width:50%;display:flex;padding-left:20px;padding-top:10px;position:relative;top:-47px}.paging button{background:none;border:none;color:rgba(0,0,0,0.5);margin-right:10px;border-radius:3px;cursor:pointer}.paging button.active{color:#ff554a}

.status-column{display:flex;flex-direction:column;align-items:center;border-right:1px solid #ccc}.status-column h2{margin-bottom:2rem}.status-column div{margin-bottom:1.5rem;font-weight:bold;color:#ff554a}.status-column--non-border{border-right:none}.status-box{display:flex;flex-direction:column;align-items:center;padding:16px}

.main-list{display:flex;flex-direction:column;justify-content:center;height:100%}.main-list__item{display:flex;justify-content:space-between;padding:0 24px;font-size:20px}.main-list__item+.main-list__item{margin-top:24px}.main-list__item-name{font-weight:bold}.main-list__item--primary .main-list__item-name{color:#ff554a}.main-list__item--center{justify-content:center;text-align:center}

.login-screen{display:flex;align-items:center;justify-content:center;width:100vw;height:100vh;background-color:#fafafa}.login-screen .login-panel{max-width:400px;width:100%}.login-screen .login-panel .MuiPaper-root{padding:24px}

.ql-container{font-family:"NotoSansKR";font-size:16px;border:none;max-height:400px;overflow-y:auto}.ql-container .ql-editor{padding:20px;min-height:300px}

.MuiDialog-paper{width:100%}

.orgModify .header{margin-top:24px;margin-bottom:40px;display:flex;align-items:center}.orgModify .header img{display:block;border-radius:4px;width:auto;height:50px;object-fit:cover;object-position:center;margin-right:24px}

